<template>
	<div id="paotui-feerule">
		<public-header title="计费规则"></public-header>
		<div class='content'>
			<div class="rule-wrap">
				<div class='rule-section' v-for="(feeItem, index) in feeRule" :key="index">
					<p class='title'>{{feeItem.title}}</p>
					<div class='rule-repeater'>
						<div class='rule-item' v-for="(childItem, childIndex) in feeItem.items" :key="childIndex">
							<div class="step-detail">
								<p class='step-title'>{{childItem.note}}</p>
								<p class="step-price">{{childItem.fee_cn}}</p>
							</div>
						</div>
					</div>
				</div>
				<div class='rule-section'>
					<p class='title'>溢价说明</p>
					<div class='introduction'>
						在恶劣天气、下单高峰等情况下，为促进骑手接单，平台可能会临时适当调整价格，以保障良好的服务体验。
					</div>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from "@/components/header"
	import PublicFooter from "@/components/footer"
	export default {
		name: "paotuiFeeRule",
		components: {
			PublicHeader,
			PublicFooter
		},
		data() {
			return{
				showPreLoading: true,
				feeRule: {}
			}
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'errander/diy/feeRule',
					data: {
						id: this.id,
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return false;
					}
					this.feeRule = result.message.feeRule;
				})
			},
		},
		created() {
			this.query = this.$route.query;
			if(this.query) {
				this.id = this.query.id;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#paotui-feerule .content{
		position: absolute;
		top: 46px;
		left: 0;
		right: 0;
		bottom: 0;
		line-height: 1;
		color: #333;
		background: #f4f4f4;
	}
	#paotui-feerule .content .rule-wrap{
		padding: 0 17px;
		padding-bottom: 20px;
		background: #fff;
	}
	#paotui-feerule .content .rule-wrap .rule-section{
		padding: 17px 0 6px;
	}
	#paotui-feerule .content .rule-wrap .title{
		color: #333;
		line-height: 44px;
		font-size: 17px;
	}
	#paotui-feerule .content .rule-wrap .base-price{
		font-size: 14px;
		float: right;
	}

	#paotui-feerule .content .rule-wrap .rule-repeater {
		list-style-type: none;
		padding: 0;
		margin: 0;
	}
	#paotui-feerule .content .rule-section .step-detail {
		display: flex;
		justify-content: space-between;
		margin: 9px 0;
		height: 22px;
		line-height: 22px;
		font-size: 14px;
	}
	#paotui-feerule .content .rule-section .step-detail .step-title {
		color: #666;
	}
	#paotui-feerule .content .rule-section .step-detail .step-price {
		color: #333;
	}
	#paotui-feerule .content .rule-wrap .rule-section .introduction {
		color: #666;
		line-height: 22px;
		font-size: 14px;
	}
</style>


